<template>
  <div class="mine-main">
    <div :class="{'search-open-contact':$store.state.headerStatus}">
      <!-- 我的-姓名和头像 begin -->
      <div class="mall-mine-head lineGradient" @click="goqyxx()">
        <div class="mine-info">
          <div class="mine-tx">
            <van-image :src="$store.state.mh.mhUserInfo.tx_url" />
          </div>
          <div class="mine-name">{{ $store.state.mh.mhUserInfo.name }}</div>
        </div>
      </div>
      <!-- 我的-姓名和头像 end -->

      <div v-if="menuList && menuList.length>0" class="content">
        <div class="minemenu-list box">
          <dl @click="goPage('我的订单',0)">
            <dt><img src="@/assets/images/mine/icon-dfk.png"></dt>
            <dd>全部</dd>
            <em v-if="allCount" class="num">{{ allCount }}</em>
          </dl>
          <dl @click="goPage('我的订单',1)">
            <dt><img src="@/assets/images/mine/icon-dfh.png"></dt>
            <dd>待发货</dd>
            <em v-if="dfhCount" class="num">{{ dfhCount }}</em>
          </dl>
          <dl @click="goPage('我的订单',2)">
            <dt><img src="@/assets/images/mine/icon-dsh.png"></dt>
            <dd>待收货</dd>
            <em v-if="dshCount" class="num">{{ dshCount }}</em>
          </dl>
          <!-- <dl @click="goOrderEvaluationList()"> -->
          <dl @click="goPage('我的订单',3)">
            <dt><img src="@/assets/images/mine/icon-dpj.png"></dt>
            <dd>已签收</dd>
            <em v-if="dpjCount" class="num">{{ dpjCount }}</em>
          </dl>
          <!-- <dl @click="goorderservicelist()">
            <dt><img src="@/assets/images/mine/icon-sh.png"></dt>
            <dd>退换/售后</dd>
          </dl> -->
        </div>
      </div>

      <div v-if="menuList && menuList.length>0" class="content">
        <div class="mine-list common-main box">
          <div v-for="(menu,index) of menuList" :key="index">
            <van-cell class="cell-list" is-link @click="goPage(menu.meta.title)">
              <template slot="title">
                <span :class="getClass(menu.name)">{{ menu.meta.title }}</span>
              </template>
            </van-cell>
          </div>
        </div>
      </div>
      <div v-else class="negative1">
        <div class="content">
          <div class="common-main box">
            <div class="status" style="margin: 40px 0;">
              <div class="status-icon no-authority" />
              您没有访问权限！
              <div class="status-text">无操作权限，请与系统管理员联系</div>
            </div>
          </div>
        </div>
      </div>

      <!-- 退出按钮 begin -->
      <div class="loginout-button">
        <van-button type="default" @click="goHome()">返回首页</van-button>
      </div>
      <!-- 退出按钮 begin -->

      <div class="common-space" />
      <FooterNavMall />
      <transition mode="out-in" name="router-slid">
        <!-- <router-view></router-view> -->
      </transition>

    </div>
  </div>
</template>

<script land="script" scoped>
import FooterNavMall from '@/components/footerNav/footerNavMall.vue'
import { getOrderCount, getPermission } from '@/store/module.js'
import { mapState } from 'vuex'
import { pageRouter } from '@/common/js/mixin.js'
export default {
  name: 'MallMine',
  components: {
    FooterNavMall
  },
  mixins: [pageRouter],
  data() {
    return {
      menuList: [],
      allCount: '', // 全部订单数量
      dshCount: '', // 待收货订单数量
      dfhCount: '', // 代发货订单数量
      dpjCount: '', // 待评价订单数量
      profiletitle: '我的',
      username: '登录/注册', // 用户名
      resetname: '',
      mobile: '暂无绑定手机号', // 电话号码
      balance: 0, // 我的余额
      count: 0, // 优惠券个数
      pointNumber: 0, // 积分数
      avatar: '', // 头像地址
      showXzdz: false
    }
  },

  computed: {
    // ...mapState([
    //   'userInfo',
    // ])
    // 取数据
    ...mapState({

      isLogin: state => state.user.isLogin,

      userInfo: state => state.user.userInfo
    })
  },
  watch: {
    userInfo: function(value) {
      this.initData()
    }
  },
  mounted() {
    this.initData()
  },
  created() {
    this.getCount()
    this.getMenu()
  },
  methods: {
    getClass(name) {
      return 'mine-title ' + name
    },
    goHome() {
      this.$router.push('MhHome')
    },
    // 获取当前用户权限菜单
    getMenu() {
      console.log('sataete======', this.$store.state)
      const postData = { ryId: this.$store.state.mh.mhUserInfo.id, jsBs: 'VUE' }
      getPermission.data = postData
      this.asyncApiModule(getPermission).then(res => {
        if (res !== null) {
          this.menuList = JSON.parse(res).router[0].children
        }
      })
        .catch((e) => {
          console.info(e)
          // this.loading = false
        })
    },
    // 获取订单数量数据信息
    getCount() {
      this.asyncApiModule(getOrderCount).then(res => {
        this.allCount = (res.allCount === null ? 0 : res.allCount)
        this.dshCount = (res.dshCount === null ? 0 : res.dshCount)
        this.dfhCount = (res.dfsCount === null ? 0 : res.dfsCount)
        this.dfkCount = (res.dzfCount === null ? 0 : res.dzfCount)
        this.dpjCount = (res.dpjCount === null ? 0 : res.dpjCount)
      })
        .catch(() => {
          this.loading = false
        })
    },
    initData() {
      if (this.isLogin) {
        // this.avatar = this.userInfo.avatar;
        this.username = this.userInfo.zzmc
        // this.mobile = this.userInfo.mobile || '暂无绑定手机号';
        // this.balance = this.userInfo.balance;
        // this.count = this.userInfo.gift_amount;
        // this.pointNumber = this.userInfo.point;
      } else {
        this.username = '登录/注册'
        // this.mobile = '暂无绑定手机号';
      }
    },
    goAddresslist() {
      this.$router.push({
        name: 'AddressList'
      })
    },
    goPage(name, cs) {
      if (name === '我的订单') {
        this.$router.push({
          name: 'MyOrder',
          params: {
            cs: cs
          }
        })
      }
      if (name === '发票模板') {
        this.$router.push({
          name: 'InvoiceList'
        })
      }
      if (name === '订单审批') {
        this.$router.push({
          name: 'OrderApproval'
        })
      }
      if (name === '收货地址') {
        this.$router.push({
          name: 'AddressList'
        })
      }
      if (name === '售后服务') {
        this.$router.push({
          name: 'OrderServiceList'
        })
      }
      if (name === '统一收货地址') {
        this.$router.push({
          name: 'TyAddressList'
        })
      }
      if (name === '统一发票模板') {
        this.$router.push({
          name: 'TyInvoiceList'
        })
      }
      if (name === '询价') {
        this.$router.push({
          name: 'XjMemberList'
        })
      }
      if (name === '竞价') {
        this.$router.push({
          name: 'BidMemberList'
        })
      }
    },
    goOrderEvaluationList() {
      this.$router.push({
        name: 'OrderEvaluationList'
      })
    },
    goOrderServiceList() {
      this.$router.push({
        name: 'OrderServiceList'
      })
    },
    goInvoiceList() {
      this.$router.push({
        name: 'InvoiceList'
      })
    },
    goOrderApproval() {
      this.$router.push({
        name: 'OrderApproval'
      })
    },
    goBidMemberList() {
      this.$router.push({
        name: 'BidMemberList'
      })
    },
    showPopupXzdz() {
      this.showXzdz = true
    }
  }
}
</script>

<style lang="css" scoped>

</style>
